import React from 'react';


const Contact = () => {
  return (
    <div className="container-fluid text-center">
      <h1>Contact Us</h1>
      <h3>Please get in touch! Please don't call. Or email.  Figure out some other way.  Ask a friend?</h3>
    </div>
  )
}

export default Contact;


